/* @import url('https://fonts.googleapis.com/css2?family=Poppins:wght@600&display=swap'); */

* {
    margin: 0;
    padding: 0;
    font-family: 'Poppins', sans-serif !important;
}

body {
    font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    background: #111;
}
.banner {
    position: relative;
    background-repeat: no-repeat;
    z-index: -10;
    width: 100%;
    height: 448px;
    color: #ffffff;
    /* margin-top: 50px; */
}

.content {
    padding-top: 140px;
    height: 190px;
    padding-left: 15px;
}

.banner .title {
    font-size: 3rem;
    font-weight: 800;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    padding-bottom: 0.3rem;
}

.button {
    color: #ffffff;
    outline: none;
    border: none;
    font-weight: 700;
    border-radius: 5px;
    padding-left: 2rem;
    padding-right: 2rem;
    padding-top: 0.5rem;
    padding-bottom: 0.5rem;
    background: rgba(51, 51, 51, 0.5);
    cursor: pointer;
    margin-right: 1rem;
}

.button:hover {
    color: black;
    background: rgba(255, 255, 255, 0.4);
    transition: all 0.3s ease;
}

.description {
    width: 45rem;
    line-height: 1.3;
    padding-top: 1rem;
    font-size: 1rem;
    height: 100px;
    z-index: -7;
    overflow: scroll;
    overflow-x: hidden;
    max-width: 360px;
}

.description::-webkit-scrollbar {
    visibility: hidden;
}

.banner .content .star progress {
    -webkit-mask: url(https://cdn-icons-png.flaticon.com/512/156/156855.png);
            mask: url(https://cdn-icons-png.flaticon.com/512/156/156855.png);
    -webkit-mask-size: cover;
            mask-size: cover;
    /* mask-size: 30%; */
    -webkit-mask-position: center;
            mask-position: center;
    -webkit-mask-repeat: no-repeat;
            mask-repeat: no-repeat;
    /* object-fit: contain; */
}

progress::-moz-progress-bar {
    background: green;
}

.banner .content .star progress {
    position: absolute;
    -webkit-appearance: none;
    appearance: none;
    /* color: black; */
    width: 150px;
    height: 50px;
    right: 20px;
    bottom: 0px;
}

progress::-webkit-progress-value {
    background: green;
}

.fade-bottom {
    height: 7.4rem;
    z-index: -5;
    background-image: linear-gradient(180deg, transparent, rgba(37, 37, 37, .61), #111);
}
.navbar {
    position: fixed;
    top: 0;
    width: 100%;
    height: 60px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    z-index: 10;
    background: rgba(0, 0, 0, 0);
    transition: all 0.3s ease;


}

.scrolled {
    transition: all 0.3s ease;
    background: rgba(0, 0, 0, 0.2) !important;
    box-shadow: inset 0 -8px 40px -6px black;
    -webkit-backdrop-filter: blur(10px);
            backdrop-filter: blur(10px);
}

.netflix-logo {
    /* position: fixed; */
    /* left: 20px; */
    margin-left: 15px;
    width: 80px;
}

.avatar {
    /* position: fixed; */
    /* right: 20px; */
    margin-right: 15px;
    height: 30px;

}
.rowpost{
    /* height: 100px; */
    color: #ffffff;
    overflow: hidden;
    /* z-index: 101; */
}

.rowpost .title{
    padding-left: 15px;
    font-family: Arial;
}

.row{
    display: flex;
    overflow-y: hidden;
    overflow-x: scroll;
    
}

.row::-webkit-scrollbar{
    display: none;
}

.rowpost .image{
    /* height: 10rem; */
    margin: 10px;
    border-radius: 1rem;
    cursor: pointer;
    /* box-shadow: 5px 5px 10px #252525,-5px -5px 10px #303030; */
    transition: all 0.15s ease;
}

.rowpost .image:hover{
    -webkit-transform :scale(1.07);
            transform :scale(1.07);
    z-index: 7;
    /* transition: all 0.2s ease; */
}


